<template><div>


<div v-if="cur_lesson == 'phrases'">
	<div :class="$css.title"> Phrases </div>

	<p> Here are some example phrases and sentences that can give you the general picture of what the language is like and how it works. </p>

	<PAT :pairs="phrases"/>
</div>




<div v-else-if="cur_lesson == 'simple-sentences'">

	<div :class="$css.title"> Making simple sentences </div>

	<p> The good news is that the word order in Nao is quite similar to English. </p>

	<PAT :pairs="{'me eat/drink rice': 'I eat rice'}"/>

	<p> 
	So as you can see, it’s simply Subject + Verb + Object. By default, the verb has no tense so the sentence above could mean
	<span class="english_word"> I ate rice </span>
		or
	<span class="english_word"> I am eating rice </span>
		or
	<span class="english_word"> I will eat rice, </span>
	etc. You just guess the tense from context. Now take a look at these sentences:
	</p>

	<PAT :pairs="{'dog see/look me' : 'A dog is looking at me.', '\'sara married \'piter' : 'Sara is married to Peter.'}"/>

	<p>
	In English, prepositions are often used in conjunction with verbs, whereas in Nao, prepositions are often built into the verb.
	Also, Nao doesn’t use anything like the articles a/the and the noun by default could be singular or plural, so for example,
	<span :class="$css.naoword"> dog </span> could mean <span class="english_word"> a/the dog(s) </span>.
	</p>

	<p> To make negation, just put no in front of the verb </p>

	<PAT :pairs="{'\'jon not eat/drink rice' : 'John doesn\'t eat rice.'}"/>

	<p> To turn the sentence into a question, put <nao pronounce n="QUESTION"/> in the beginning. </p>

	<PAT :pairs="{'QUESTION you happy' : 'Are you happy?'}"/>

	<p>
	Note that:
	<br>1. Nao doesn’t use capital letters, to minimize the number of different characters.
	<br>2. The sentences are separated with a dash (−) or hyphen (-) because without the capital letters, the dot is too small to separate sentences, which makes reading more difficult.
	When writing a text, starting the first sentence with a dash and ending the last sentence with a dash is optional.
	<br>3. Nao doesn’t use sentence finals like the question mark, exclamation mark, period or ellipsis. Using them is considered informal.
	</p>

	<PAT :pairs="{'− happy-meet − you named what −' : 'Hello. What is your name?'}"/>

	<p> The question mark is unnecessary because every question sentence contains <nao n="QUESTION"/>  and/or  <nao n="what"/>. The other sentence finals are not used because their usage differs dramatically from language to language. </p>

</div>



<div v-else-if="cur_lesson == 'opposite'">

	<div :class="$css.title"> Building the opposite </div>

	<p> One of the most powerful features of Nao is forming the opposite meanings. To change the meaning to the opposite,
	alternate <span :class="$css.naoword"> i / u </span>. For example: </p>

	<PAT :little_columns="[
		{'warm/hot': 'warm/hot', 'cold': 'cold' },
		{'female': 'female', 'male': 'male' },
		{'previous': 'previous', 'next': 'next' }
	]" />

	<p> Note that to construct a word with the opposite meaning, the vowel that is altered must be the last vowel in the elementary word in question. This means that dia and dua are not opposites. In fact, they can never be opposites, because neither of the words' final vowels are <span :class="$css.naoword"> i / u </span>. </p>

	<p> Also note that even if a word has <span :class="$css.naoword"> i / u </span> as the last vowel, it doesn’t mean that it has an opposite for sure. Many words just don’t have an opposite because it would make no sense. For example:
		<span :class="$css.naoword"> rus </span> (Russian) and <span :class="$css.naoword"> ris </span> (rice)
	are of course not opposites and it is obvious from their meaning that they cannot possibly have any opposite. </p>

	<p> As a natural result, this trick automatically works for compound words as well: </p>

	<PAT :little_columns="[
		{'hagalai': 'white', 'hagalau': 'black'},
		{'diakin': 'yesterday', 'diakun': 'tomorrow' },
		{'nejantalli': 'reward', 'nejantallu': 'punish' },
		{'soinienai': 'summer', 'soinienau': 'winter' },
	]" />


	<div :class="$css.title"> Opposite vs negation </div>

	<p> Some beginners mix up the opposite with a simple negation. In reality, most of the cases those are two different things. Let’s take a look at three possible situations: </p>

	<Awtable :arr="[
		['Original word', 'Opposite', 'Negation'],
		['mik = right side', 'muk = left side', 'nomik = not right side'],
		['bai = buy', 'bau = sell', 'nobai = not buy'],
		['pau = parent', 'pai = child', 'nopau = not parent'],
		['li = good', 'lu = bad', 'noli = not good'],
		['bul = strong', 'bil = weak', 'nobul = not strong'],
		['dei = true', 'deu = false', 'nodei = not true'],
		['mai = be allowed', 'mau = be forbidden', 'nomai = be not allowed'],
	]" />

	<p> You can find specific rules about building the opposite in the Full Set of Rules II.8 </p>
</div>



<div v-else-if="cur_lesson == 'ke'">

	<div :class="$css.title"> Questions with ke </div>

	<p> The word ke simply means what. Let’s see how we can use it in a sentence: </p>

	<PAT :pairs="{'you eat/drink what?' : 'What are you eating? (literally: you eat what)'}"/>

	<p> You can see that <nao n="what"/> is at the end of the sentence. It’s because we must keep the word order Subject + Verb + Object. You can think like this: <nao n="what"/> should be placed in the same place as the answer word. </p>

	<PAT :pairs="{'\'jon gin ke' : 'What is John eating?', '\'jon gin pot' : 'John is eating potatoes.'}" />

	<p> You can see that <nao n="what"/> (the question word) and <nao n="potato"/> (the answer word) are at the same position. More examples: </p>

	<PAT :pairs="{
		'female-that go that for_the_sake buy what' : 'She is going there in order to buy what?',
		'female-that go that for_the_sake buy potato' : 'She is going there in order to buy potatoes.',
		'what cause-feel-in_danger male-that' : 'What scared him?',
		'dencul nenabun muta' : 'Lightning scared him.'
	}" />


	<p> Even if the sentence already has <nao n="what"/>, you can additionally put the question particle ca in the beginning, it is optional. </p>

	<PAT :pairs="{'(QUESTION) you eat/drink what': 'What are you eating?'}" />

	<p> It can also be used in connection with nouns, meaning <span class="english_word"> what/which </span>. For example: </p>

	<PAT :pairs="{'pot ke' : 'which potatoes', 'dio ke' : 'what story'}" />

	<p> All questions words are derived from <nao n="what"/>. Most commons ones are: </p>

	<PAT :pairs="{
		'person-what' : '(what person) = who',
		'time-what' : '(what time) = when',
		'because-what' : '(because of what) = why',
		'be_located-what' : '(be located in/at what) = where',
		'quantity-what' : '(of what quantity) = how much/many'
	}" />
</div>




<div v-else-if="cur_lesson == 'clauses'">

	<div :class="$css.title"> Clauses </div>

	<p> What is a clause? It’s basically a sentence inside another sentence. In English, we often put <span class="english_word"> that </span> in front of the clause. In Nao, we use <nao n="CLAUSE" />: </p>

	<PAT :pairs="{
		'\'jon know CLAUSE \'alis not work day-NOW' : 'John knows that Alice doesn’t work today.',
		'\'alis sad CLAUSE \'jon not eat/drink' : 'Alice is sad that John doesn’t eat.',
		'me see/look \'alis work' : 'I see/watch Alice working.',
		'QUESTION you want CLAUSE me go' : 'Do you want me to go?',
	}" />

	<p> Similarly with the English marker <span class="english_word">that</span>, in Nao <nao n="CLAUSE" /> is optional when it is easy to guess. </p>
	<p> It is also possible to make a question clause using <nao n="QUESTION" />, equivalent to <span class="english_word">whether / if</span> in English: </p>

	<PAT :pairs="{
		'\'jon know QUESTION \'alis work day-NOW' : 'John knows whether Alice works today.',
	}" />

	<p> Note that if the question clause has ke, then ca is optional. </p>

	<PAT :pairs="{
		'me not understand (QUESTION) you want what' : 'I don’t understand what you want.',
	}" />
</div>




<div v-else-if="cur_lesson == 'location-and-direction'">
	<div :class="$css.title"> pa </div>

	<p> Originally it is a verb meaning <span class="english_word">to be located (in/at)</span>, so we can say: </p>

	<PAT :pairs="{'me be_located \'boston' : ' I am in Boston'}"/>

	<p> But we can also use it as an adverb: </p>

	<PAT :pairs="{'me work be_located \'boston' : 'I work in Boston'}"/>

	<p> So the word <nao n="be_located"/> will usually translate to English as <span class="english_word">in/at</span>. </p>

	<p> To talk about the location with more precision, the following words might be useful: </p>

	<PAT :little_columns="[
		{'inside': 'inside', 'outside': 'outside' },
		{'front': 'front', 'backside': 'back' },
		{'right': 'right', 'left': 'left' }
	]" />

	<p> All of the above words are nouns, so they are often used along with <nao n="be_located"/>. </p>

	<PAT :pairs="{
		'stick_shaped-write be_located \'mesa' : 'The pen is located … the table (just guess the preposition).',
		'stick_shaped-write be_located top \'mesa' : 'The pen is on the table.',
		'stick_shaped-write be_located bottom \'mesa' : 'The pen is under the table.',
		'stick_shaped-write be_located inside \'mesa' : 'The pen is in the table (maybe in the drawer)',
	}" />

	<div :class="$css.title"> tu (to), ti (from) </div>

	<p> We can use that pair to talk about directions. For example: </p>

	<PAT line :pairs="{
		'go-fast to place-buy' : 'Run to the shop.',
		'go-fast to inside place-buy' : 'Run into the shop.',
		'go-fast to outside place-buy' : 'Run out of the shop.',
		'go-fast to backside place-buy' : 'Run [to] behind the shop.',
		'GIANT-do_voice from building' : 'Shout from the house.',
		'GIANT-do_voice from backside building' : 'Shout from behind the house.',
	}" />

</div>

<div v-else-if="cur_lesson == 'colors'">
	<div :class="$css.title"> Colors </div>
	<p> The color system in Nao is very powerful – it allows you to express any possible color. It is based on HSL (hue-saturation-luminosity) model. Look at the color wheel below and you can notice six basic colors (like the seven colors of the rainbow but orange being an intermediate color). Try to click on the colorwheel and you'll understand how it works. </p>

	<div class="d-flex justify-center pb-5">
		<Colorwheel :size="cw_size" />
	</div>

	<p> So how does it work? First we have to memorize the 6 basic colors: </p>

	<PAT line no_pronounce :pairs="{
		'ha0 (hanol)' : 'red',
		'ha1 (haji)' : 'yellow',
		'ha2 (hadua)' : 'green',
		'ha3 (hatui)' : 'cyan',
		'ha4 (hafos)' : 'indigo',
		'ha5 (hapet)' : 'magenta',
	}" />

	<p> Note that for shortness, we can drop final zeroes: </p>
	
	<p class="pl-8"> <nao o="ha10 = ha1" />  </p>

	<p> Then we can easily get intermediate colors. For example, between <nao n="color-0-0"/> and <nao n="color-1-0"/>: </p>

	<PAT :pairs="{
		'ha05' : 'orange',
		'ha08' : 'gold',
	}" />

	<p> Then we can apply the words <nao n="light"/> (light) or <nao n="dark"/> (dark) to create more colors: </p>

	<PAT :pairs="{
		'ha05 lau' : '(dark orange) = brown',
		'ha5 lai' : '(light magenta) = pink',
	}" />

	<p> Then we can also use the word <nao n="weak"/> (weak) to lower the saturation (mix with grey): </p>

	<PAT :pairs="{
		'hanolbil' : 'greyish red',
	}" />

	<p>There are three special colors:</p>

	<PAT line :pairs="{
		'hagalai' : '(completely light color) = white',
		'hagalau' : '(completely dark color) = black',
		'hagabil' : '(completely weak color) = grey',
	}" />

	<p> You can also enter any color in the dictionary and it will show it to you. <router-link to="/dict/hanolpetlau_hagalai_ha35_lai7"> Try it! </router-link> </p>


</div>

<div class="d-flex flex-wrap justify-space-around">
	<div class="cp ma-5" :class="{invis: i == 0}" @click="i--"> ← Previous </div>
	<div class="cp ma-5" :class="{invis: i == all_lessons_routes.length - 1}" @click="i++"> Next → </div>
</div>

</div></template>

<script>

import PAT from '@/components/PhrasesAndTranslations'
import nao from '@/components/Naoword'
import Awtable from '@/components/Awtable'
import Colorwheel from '@/components/Colorwheel'

export default {
	components: {PAT, nao, Awtable, Colorwheel},
	data() {
		this.all_lessons = [
			{route: 'phrases', name: 'Phrases' },
			{route: 'simple-sentences', name: 'Making simple sentences' },
			{route: 'opposite', name: 'Building the opposite' },
			{route: 'ke', name: 'Questions with ke' },
			{route: 'clauses', name: 'Clauses' },
			{route: 'location-and-direction',  name: 'Location and direction' },
			{route: 'colors', name: 'Colors'},
		];

		this.all_lessons_names = this.all_lessons.map(x => x.name);
		this.all_lessons_routes = this.all_lessons.map(x => x.route);

		return {
			//i: null,
			i: this.all_lessons_routes.indexOf(this.$route.params.lesson),
		}
	},
	watch: {
		i(nv, ov) {
			this.$router.push('/textbook/' + this.all_lessons_routes[this.i]).catch(function(err){});
			//window.scrollTo(0, 0);
		},
		$route (to, from){
	        this.i = this.all_lessons_routes.indexOf(this.$route.params.lesson);
	    }
	},
	methods: {

	},
	created() {
		if (this.i == -1) this.i = 0;
		//this.i = this.all_lessons_routes.indexOf(this.$route.params.lesson) || 0;
	},
	computed: {
		cw_size() {
			return {xs:0.4, sm:0.6, md:0.65, lg:0.8, xl:0.9}[this.$vuetify.breakpoint.name];
		},
		cur_lesson() {
			return this.all_lessons_routes[this.i];
		},
		phrases() {
			let res = {};

`happy-meet			Hello!
me named 'jon		My name is John.
you named what		What is your name?
you feel-in_state what			How is your mood?
good				Good.
bad					Bad.
happy-with-respect-do-good		Thank you.
feel-bad-do-bad			I'm sorry.
not-deserve				It's nothing (as a reply to an apology or a thank-you)
true				True (normally translates as "yes")
false				False (normally translates as "no")
me understand		I understand.
that dog me			That's my dog.
that what				What is that?
QUESTION that cat you		Is that your cat?
QUESTION building 'jon high		Is John's house tall?
'piter sit			Peter is sitting.
you not sit			You are not sitting.
me like cat			I like cats.
you like what		What do you like?
QUESTION cat like sleep		Do cats like to sleep?
QUESTION 'jon dislike dog		Does John dislike dogs?
'jeni go sleep		Jenny goes to sleep.
'piter want sit		Peter wants to sit.
also 'tom want sit	Tom, too, wants to sit.
me FUTURE go 'madrid	I will go to Madrid.
me PAST go 'istanbul	I went to Istanbul.
ask go				Go!
ask not buy that		Don't buy that!
male-that have four banana	He has four bananas
'alis be_located 'london	Alice is in London`
			.split('\n').map( function(line) {
				let s = line.split(/\t+/);
				res[s[0]] = s[1];
			});
			return res;
		},
	},
}
</script>

<style scoped>

.invis {
	visibility: hidden;
}

div {
	xborder: 1px solid black;
}

.cp {
	cursor: pointer;
	font-weight: bold;
}

</style>