<template><div>

<div :class="$css.title"> Comparing Nao with Esperanto </div>

<ol>

<li>
Esperanto has great potential for word construction. Nevertheless, it has a countless number of roots that could be expressed through other roots without losing any meaning. For example:

<div class="pa-5">
	<div v-for="r in `studento – lernulo, hospitalo – kuracejo, kuraci – sanigi, murdi − mortigi, oftalmologo – okulisto`.split(', ')"> {{r}} </div>
</div>

We can find thousands of such examples. Esperanto dictionaries contain tens of thousands of roots. Memorizing all of them is a big barrier for mastering the language. Nao aims to avoid excessive roots, i.e. those that can be constructed from the simpler ones. It's worth noting, however, that in Esperanto in many cases words would get too long and hard to pronounce because the Esperanto roots on average are quite long. In Nao every root consists of one syllable which allows for constructing words consisting of a long chain of roots.

<p> Quite often Esperanto doesn't use word construction where it would dramatically help memorization. For example, the names of months and days of the week can be constructed from the numbers (like in Chinese and Vietnamese). Nao follows that strategy.
</p>

<p> In Esperanto one can often see a group of roots that are close in meaning. For example, the roots <b>komik-, rid-, komedi-, ŝerc-</b> all describe closely related concepts but are completely unrelated gramatically. In Nao, all those concepts are logically constructed from the elementary word <span :class="$css.naoword">hal</span> = «laugh».
</p>
<span :class="$css.naoword"></span>
<div class="pa-5">
	<div>ridi = <span :class="$css.naoword">hal</span></div>
	<div>komika = <span :class="$css.naoword">dohal</span> (<i>such that is laughed at</i>)</div>
	<div>komedio = <span :class="$css.naoword">diohal</span> (<i>funny story</i>)</div>
	<div>ŝerci = <span :class="$css.naoword">tuanehal</span> (<i>try to make laugh</i>)</div>
</div>


One more example: the roots <b>tim-, kuraĝ-, danĝer-, sav-, sekur-, protekt-, defend-, gard-, risk-, minac-, rifuĝ-</b> in Nao can be constructed from the elementary word <span :class="$css.naoword">bun</span> = «be in danger».</p>

<div class="pa-5 dlist">
	<div>timi → <span :class="$css.naoword">nabun</span> <br>(<i>experience a feeling/emotion signalizing about being in danger</i>)</div>
	<div>kuraĝa → <span :class="$css.naoword">peunabun</span> <br>(<i>prone to not be scared</i>)</div>
	<div>savi → <span :class="$css.naoword">nenogabun</span> <br>(<i>cause to not be in complete danger</i>)</div>
	<div>sekura → <span :class="$css.naoword">dobin</span> <br>(<i>such that one is not in danger from</i>)</div>
	<div>protekti/defendi → <span :class="$css.naoword">nebin</span> <br>(<i>cause to not be in danger</i>)</div>
	<div>riski → <span :class="$css.naoword">fasebun</span> <br>(<i>do actions such that oneself is in danger</i>)</div>
	<div>singarda → <span :class="$css.naoword">tuabin</span> <br>(<i>aim to not be in danger</i>)</div>
	<div>rifuĝejo → <span :class="$css.naoword">mabin</span> <br>(<i>a specialized place for being safe</i>)</div>
</div>

<p>This way, the number of elementary words in Nao is much lower but the expressing ability is not at all inferior to Esperanto.
</p>
</li>
<li> In Esperanto, the male gender of the word is often in-built in the word by default and can be changed to female by applying the suffix <b>-in-</b>. This is inconvenient and rightly leads to critisism. In Nao, no word contains information about the gender; instead, the gender is specified separately with words <b>mi</b> = «female», <b>mu</b> = «male». For example:

<div class="pa-5">
	<span :class="$css.naoword">pau</span> = parent<br>
	<span :class="$css.naoword">mipau</span> = mother<br>
	<span :class="$css.naoword">mupau</span> = father<br>
</div>
<div class="pa-5">
	<span :class="$css.naoword">paikio</span> = nephew/niece<br>
	<span :class="$css.naoword">mipaikio</span> = niece<br>
	<span :class="$css.naoword">mupaikio</span> = nephew<br>
</div>

<span class="english_word"></span>
<p> Another word worth noting is <span :class="$css.naoword">data</span> literally meaning <span class="english_word">that person</span> - it is a gender-neutral equivalent of <span class="english_word">he/she</span>. </p>

</li>
<li>
<p> Often the roots in Esperanto have a vague meaning or a lot of meanings. The situation also gets worse when people under the influence of their own cultural and linguistic background start putting their own nuances into the words, especially in abstract words. For example, in some dictionaries it is written that <b>studento</b> means <span class="english_word">student</span> while in many other dictionaries it is translated as <span class="english_word">university student</span> and <span class="english_word">academic</span>. In Nao, each word only has one meaning and such ambiguity is impossible because the number of elementary words is small and each of them is strictly defined, while the combination meaning is strictly derived from the elements.
</p>
</li>
<li>
<p> Esperanto uses 23 consonants while Nao only uses 14. Also, consonants clusters are easier and much less frequent in Nao. It makes Nao easier to pronounce, especially for non-Europeans. Moreover, all Nao letters are standard Latin alphabet which facilitates typing and doesn't scare away beginners.</p>
</li>

<li>
Esperanto grammar has some unpleasant moments:

<ol type="a">
<li> Antonyms are built with the prefix mal-; that's long and causes a lot of critisism. In Nao, the antonym is built with alternating i/u. <br>
For example: <span :class="$css.naoword">li</span> = bona, <span :class="$css.naoword">lu</span> = malbona</li>
<li> The definite article <b>la</b> which shouldn't be in an international language at all. Nao doesn't use definite or indefinite articles. </li>
<li> The accusative ending <b>-n</b> gives headache to a lot of people. Nao has an accusative marker but it is optional (used only in case of non-standard word order).</li>
<li> 
	The omnipresent <b>estas</b> that we see in almost every sentence is long and annoys many people. In Nao, the copula <span :class="$css.naoword">bio</span> is shorter and is allowed to omit if the sentence remains clear.</li>
<li> The tense suffixes <b>-is,-as,-os</b> are mixed up by many people because of high similarity. In Nao, the verb tense is not obligatory as in most situations it is clear from the context.</li>
</ol>
</li>

<li>
Esperanto doesn't have official transliteration rules to other languages. As a result, we have chaos when the same name can be written in many different ways in Esperanto. For example:

<div class="pa-5">
	<div> <b>Houston</b> → Hjustono / Hustono / Houstono</div>
	<div> <b>Евгения</b> → Jevgenija, Evgenija, Evgenia, Eŭgenija,...</div>
	<div> <b>香港</b> → Honkongo / Hongkongo</div>
	<div> <b>西安</b> → Ŝjiano / Siano</div>
</div>

Often the names are not transliterated at all even if some letters are not even a part of the Esperanto alphabet. This creates a lot of confusion with how to type and pronounce them. Adding <b>-o</b> to loan words which is required by Esperanto grammar often looks quite silly, too. On the contrary, Nao has <router-link to="/transliterations">official transliteration rules</router-link> for each language. Thus, each proper noun has only one correct way of writing.
</li>



</ol>


</div></template>

